<template>
  <header
    :class="[
      headerMobileFix == true ? 'c-header--mobile--fixed' : '',
      sectionScroll == 'menu' ? 'c-header--colored' : '',
    ]"
  >
    <div class="c-header" id="js-header" ref="header">
      <a class="c-header__logo--desktop" v-scroll-to="'#js-header'"></a>
      <div class="c-header__upper">
        <div class="c-header__logo">
          <img
            src="https://jeffersoncruzit.github.io/portfolio-bigchill/img/logo.png"
            alt=""
          />
        </div>
      </div>
      <div class="c-header__lower" id="js-header__lower">
        <ul>
          <li>
            <a
              :class="[
                'c-header__icon c-header__icon--menu',
                mobileScroll == 'menu' ? 'c-header__icon--menu--active' : '',
              ]"
              href="javascript:void(0)"
              v-scroll-to="'#js-menu'"
              >Our Menu</a
            >
          </li>
          <li>
            <a
              :class="[
                'c-header__icon c-header__icon--story',
                mobileScroll == 'story' ? 'c-header__icon--story--active' : '',
              ]"
              href="javascript:void(0)"
              v-scroll-to="'#js-story'"
              >Our Story</a
            >
          </li>
          <li>
            <a
              :class="[
                'c-header__icon c-header__icon--partner',
                mobileScroll == 'franchise'
                  ? 'c-header__icon--partner--active'
                  : '',
              ]"
              href="javascript:void(0)"
              v-scroll-to="'#js-franchise'"
              >Be Our Partner</a
            >
          </li>
          <li>
            <a
              :class="[
                'c-header__icon c-header__icon--location',
                mobileScroll == 'branch'
                  ? 'c-header__icon--location--active'
                  : '',
              ]"
              href="javascript:void(0)"
              v-scroll-to="'#js-branch'"
              >Branches</a
            >
          </li>
          <li>
            <a
              :class="[
                'c-header__icon c-header__icon--contact',
                mobileScroll == 'footer'
                  ? 'c-header__icon--contact--active'
                  : '',
              ]"
              href="javascript:void(0)"
              v-scroll-to="'#js-footer'"
              >Contact</a
            >
          </li>
        </ul>
      </div>
    </div>
    <div
      :class="[
        'c-header__social',
        socialScroll == 'colored' ? 'c-header__social--colored' : '',
      ]"
      id="js-headerSocial"
    >
      <ul>
        <li>
          <a
            class="c-header__socialIcon c-header__socialIcon--facebook"
            href=""
          ></a>
        </li>
        <li>
          <a
            class="c-header__socialIcon c-header__socialIcon--instagram"
            href=""
          ></a>
        </li>
        <li>
          <a
            class="c-header__socialIcon c-header__socialIcon--twitter"
            href=""
          ></a>
        </li>
      </ul>
    </div>
  </header>
</template>

<script>
import Vue from "vue";
// https://www.npmjs.com/package/vue-scrollto
import VueScrollTo from "vue-scrollto";
// var VueScrollTo = require('vue-scrollto');
Vue.use(VueScrollTo, {
  container: "body",
  duration: 500,
  easing: "ease",
  offset: 0,
  force: true,
  cancelable: true,
  onStart: false,
  onDone: false,
  onCancel: false,
  x: false,
  y: true,
});
export default {
  data() {
    return {
      headerMobileFix: false,
      headerScroll: "up",
      sectionScroll: "banner",
      socialScroll: "white",
      mobileScroll: "banner",
      scrollRecent: null,
    };
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      let scrollPosition = window.pageYOffset;
      // console.log(scrollPosition);
      const header = parseInt(document.getElementById("js-header").offsetTop);
      const lowerHeaderHt =
        parseInt(document.getElementById("js-header__lower").offsetHeight) / 2;
      //   console.log(lowerHeaderHt);
      const menu = parseInt(document.getElementById("js-menu").offsetTop);
      const menuHt = parseInt(document.getElementById("js-menu").offsetHeight);

      const story = parseInt(document.getElementById("js-story").offsetTop);
      const storyHt = parseInt(
        document.getElementById("js-story").offsetHeight
      );

      const franchise = parseInt(
        document.getElementById("js-franchise").offsetTop
      );
      const franchiseHt = parseInt(
        document.getElementById("js-franchise").offsetHeight
      );

      const footer = parseInt(document.getElementById("js-footer").offsetTop);
      const footerHt = parseInt(
        document.getElementById("js-footer").offsetHeight
      );

      const branch = parseInt(document.getElementById("js-branch").offsetTop);
      const branchHt = parseInt(
        document.getElementById("js-branch").offsetHeight
      );

      const banner = parseInt(document.getElementById("js-banner").offsetTop);
      const bannerHt = parseInt(
        document.getElementById("js-banner").offsetHeight
      );

      //   const social = parseInt(document.getElementById('js-headerSocial').offsetTop);
      const socialHt = parseInt(
        document.getElementById("js-headerSocial").offsetHeight
      );
      //   console.log(social);
      //   console.log(menu);

      /**
       * MOBILE
       */
      /**
       * fix scroll
       */
      if (scrollPosition > header) {
        this.headerMobileFix = true;
      }
      if (scrollPosition < lowerHeaderHt) {
        this.headerMobileFix = false;
      }

      /**
       * section position
       */

      if (scrollPosition >= banner && scrollPosition < banner + bannerHt) {
        this.mobileScroll = "banner";
        // console.log('banner');
      }
      if (scrollPosition >= menu && scrollPosition < menu + menuHt) {
        this.mobileScroll = "menu";
        // console.log('menu');
      }
      if (scrollPosition >= story && scrollPosition < story + storyHt) {
        this.mobileScroll = "story";
        // console.log('story');
      }
      if (
        scrollPosition >= franchise &&
        scrollPosition < franchise + franchiseHt
      ) {
        this.mobileScroll = "franchise";
        // console.log('franchise');
      }
      if (scrollPosition >= branch && scrollPosition < branch + branchHt) {
        this.mobileScroll = "branch";
        // console.log('branch');
      }
      if (scrollPosition >= footer && scrollPosition < footer + footerHt) {
        this.mobileScroll = "footer";
        // console.log('footer');
      }

      /**
       * DESKTOP
       */
      if (
        scrollPosition > menu - lowerHeaderHt &&
        scrollPosition < story - lowerHeaderHt
      ) {
        // console.log('color');
        this.sectionScroll = "menu";
      } else {
        this.sectionScroll = "others";
        // console.log('white');
      }

      if (scrollPosition > socialHt / 2 && scrollPosition < story) {
        this.socialScroll = "colored";
        // console.log('colored');
      } else {
        this.socialScroll = "white";
        // console.log('white');
      }
    },
  },
};
</script>

<style></style>
