<script>
// https://github.com/paulcollett/vue-masonry-css
import Vue from 'vue'
import VueMasonry from 'vue-masonry-css'
Vue.use(VueMasonry);

export default {
    data() {
        return {
            isVisible: false
        }
    },
    methods: {
        accordionToggle(e) {
            e.preventDefault();
            let el = e.target.parentNode;
            let isActive = el.classList.contains('c-branch__accordion--active');
            if (!isActive) {
                el.classList.add('c-branch__accordion--active');
            } else {
                el.classList.remove('c-branch__accordion--active');
            }
        },
        visibilityChanged (isVisible) {
            this.isVisible = isVisible;
        }
    }
}
</script>

